body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.event {
    width: 30%;
    height: 200px;
    border-radius: 5px;
    margin: 0 0 2.5% 0;
    overflow: hidden;
    background-size: cover;
    position: relative;
    font-family: 'Open Sans Condensed', sans-serif;
    font-size: 20px;
}

.event > .overText {
    background-color: rgba(0, 0, 0, 0.5);
    color: white;
    padding: 15px;
    position: absolute;
    bottom: 0;
    width: 100%;
}

.eventGrid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
}

.filterBars {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    margin: 25px 0;
}

.filterBar {
    height: 35px;
    text-align: left;
    margin: 2px;
    padding: 5px;
    border: 1px solid black;
    width: 15%;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    font-family: 'Open Sans', sans-serif;
}

.search {
    width: 30%;
}

.filterBar > * {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 15px;
    padding: 0;
    margin: 0;
    height: 100%;
    border: 0;
}

.filterBar > select {
    float: right;
    text-align: right;
    width: 50%;
}

.popout {
    width: 97%;
    background-color: #C99FF0;
    height: 400px;
    display: inline;
    font-family: 'Open Sans', sans-serif;
}

.popout > * {
    margin: 10px 25px 0 25px;
}

.popout > img, .desc {
    width: 45%;
    margin: 10px 0 0 25px;
    border: 1px solid black;
    background-color: black;
    border-radius: 5px;
    padding: 5px;
    height: 60%;
}

.popout > img {
    object-fit: cover;
}

.popout > .desc {
    margin: 10px 25px 0 0;
    padding: 5px;
    
    float: right;
    background-color: white;
    
}
